import React from 'react';

// Components
import SectionHeader from '../components/SectionHeader/SectionHeader.js';

export default class Contact extends React.Component {
    handleClick() {
        window.open("https://drive.google.com/file/d/12uSSogceCRtsu3sK9K21HpvPgheRwGhq/view?usp=sharing");
    }

    handleEmail() {
        window.location.href = "mailto:drcall4@outlook.com";
    }

    render() {
        return(
            <div className = "page" id = "contact">
                <SectionHeader txt = "white" sectionID = "contact-break" title = "GET IN TOUCH" />
                <p className = "contactText">
                    Interested in having a chat? You can reach me on my social media platforms (preferably LinkedIn) 
                    or send me an e-mail down below. If you want to learn more about me, check out my resume below as well.
                </p>
                <div className = "contactGrid" id = {this.props.state.windowWidth > 950 ? "contact-2": "contact-1"}>
                    <b className  = "contactButton" onClick = {this.handleClick.bind(this)}>Resume</b>
                    <b className  = "contactButton" onClick = {this.handleEmail.bind(this)}>E-mail</b>
                </div>
            </div>
        );
    }
}